<template>
  <div class="remark_container">
        <section v-if="!showLoading">
            <head-top head-title="订单备注" goBack="true"></head-top>
            <section v-if="remarkList.remarks.length" class="kuaishu_beiju">
                <div class="remark_beizu">
                    <header>
                    快速备注
                    </header>
                    <ul>
                        <li v-for="(item,index) in remarkList.remarks" :key="index" >
                            <span class="remark_item_li" v-for="(remarkItem,remarkIndex) in item" :key="remarkIndex" :class="{first:remarkIndex==0, last: remarkIndex == item.length - 1,choose:remarkText[index]&&(remarkText[index][0]==remarkIndex)}" @click="chooseRemark(index,remarkIndex,remarkItem)">{{remarkItem}}</span>
                        </li>
                    </ul>
                </div>
            </section>
            <section class="qita_beiju">
                <header class="header_style">其他备注</header>
                <textarea name="" id="" v-model="inputText" placeholder="请输入备注内容(可不填)"></textarea>
            </section>
            <div class="determine" @click="confirmRemark">确定</div>
        </section>
      <loading v-show="showLoading"></loading>
  </div>
</template>

<script>
import headTop from '../../../components/header/header'
import {mapMutations} from 'vuex'
import loading from '../../../components/common/loading'
import {getRemark} from '../../../service/getData'
export default {
    components: {
        headTop,
        loading
    },data(){
        return{
            id:null,
            sig:null,
            remarkList:null,
            showLoading:true,
            remarkText:{},
            inputText:null
        }
    },created () {
        this.id=this.$route.query.id;
        this.sig=this.$route.query.sig;
    },mounted () {
        this.initDate();
    },methods: {
        ...mapMutations([
            'CONFIRM_REMARK'//将记录备注内容存入vuex订单页调用
        ]),
       async initDate()
        {
            this.remarkList=await getRemark(this.id,this.sig);
            this.showLoading=false;
        },
        //选择快速备注
        chooseRemark(id,index,item)
        {
             //Object.values()把对象转换为键值对的数组可以循环Object.assing()把键值对数组转换为对象
            this.remarkText[id]=[index,item];
            this.remarkText=Object.assign({},this.remarkText);
        },
        //确定选择
        confirmRemark()
        {
            this.CONFIRM_REMARK({remarkText:this.remarkText,inputText:this.inputText});
            this.$router.go(-1);
        }
    }
}
</script>

<style scoped lang="scss">
@import '../../../style/mixin.scss';
.remark_container{
    position: fixed;
    left:0;
    right: 0;
    bottom: 0;
    top: 0;
    background: #f5f5f5;
    z-index: 204;
}
.kuaishu_beiju{
    background: #fff;
    margin-top: 2.1rem;
    padding:0.6rem 1rem;
    ul{
        display: flex;
        flex-wrap: wrap;
        li{
            margin-right: .6rem;
            margin-bottom: .6rem;
            span{
                @include sc(.6rem,#333);
                padding:.3rem .6rem;
                border:0.025rem solid #3190e8;
                border-left: 0;
            }
            .first{
                border-left:0.025rem solid #3190e8;
                border-top-left-radius: .2rem;
                border-bottom-left-radius: .2rem;
            }
            .last{
                border-top-right-radius: .2rem;
                border-bottom-right-radius: .2rem;
            }
        }
    }
}
.choose{
    color: #fff;
    background-color: #3190e8;
}
.qita_beiju{
    margin-top: 0.6rem;
    background: #fff;
    padding:0.6rem;
    header{
        @include sc(0.7rem,#333);
    }
    textarea{
        @include wh(100%,5rem);
        border:0.025rem solid #f1f1f1;
        background: #f1f1f1;
        padding-left:0.5rem;
        @include sc(0.7rem,#666);
        border-radius: 0.2rem;
        margin-top: 0.6rem;
    }
}
.determine{
    display: flex;
    background-color: #4cd964;
    @include sc(0.7rem,#fff);
    justify-content: center;
    align-items: center;
    border-radius: 0.4rem;
    margin:0 .6rem;
    padding:0.5rem;
}
</style>



